const moveToSomewhere = (itemToMoveClass, placeToMoveClass) => {
  document.querySelectorAll(".blog-index__post").forEach(post => {
    const placeToPutTheDate = post.querySelector(placeToMoveClass);
    if (!placeToPutTheDate) {
      return;
    }

    const item = post.querySelector(itemToMoveClass);
    if (!item) {
      return;
    }

    const newItem = item.cloneNode(true);
    newItem.classList.add("digiphy-custom");

    placeToPutTheDate.prepend(newItem);

    item.classList.add("hidden");
  });
};

export default moveToSomewhere;
